ition: absolute;\n  @if $height != 0 {\n    left: calc(50% - (#{$width} / 2));\n    top: calc(50% - (#{$height} / 2)); }\n  @else {\n    left: calc(50% - (#{$width} / 2));\n    top: calc(50% - (#{$width} / 2)); } }\n\n@mixin fa($size, $dimensions) {\n  display: inline-block;\n  font-size: $size;\n  height: $dimensions;\n  line-height: $dimensions;\n  text-align: center;\n  vertical-align: top;\n  width: $dimensions; }\n\n@mixin hamburger($dimensions) {\n  cursor: pointer;\n  display: block;\n  height: $dimensions;\n  position: relative;\n  width: $dimensions;\n  span {\n    background-color: currentColor;\n    display: block;\n    height: 1px;\n    left: calc(50% - 8px);\n    position: absolute;\n    transform-origin: center;\n    transition-duration: $speed;\n    transition-property: background-color, opacity, transform;\n    transition-timing-function: $easing;\n    width: 16px;\n    &:nth-child(1) {\n      top: calc(50% - 6px); }\n    &:nth-child(2) {\n      top: calc(50% - 1px); }\n    &:nth-child(3) {\n      top: calc(50% + 4px); } }\n  &:hover {\n    background-color: bulmaRgba(black, 0.05); }\n  // Modifers\n  &.is-active {\n    span {\n      &:nth-child(1) {\n        transform: translateY(5px) rotate(45deg); }\n      &:nth-child(2) {\n        opacity: 0; }\n      &:nth-child(3) {\n        transform: translateY(-5px) rotate(-45deg); } } } }\n\n@mixin overflow-touch {\n  -webkit-overflow-scrolling: touch; }\n\n@mixin placeholder {\n  $placeholders: ':-moz' ':-webkit-input' '-moz' '-ms-input';\n  @each $placeholder in $placeholders {\n    &:#{$placeholder}-placeholder {\n      @content; } } }\n\n// Responsiveness\n\n@mixin from($device) {\n  @media screen and (min-width: $device) {\n    @content; } }\n\n@mixin until($device) {\n  @media screen and (max-width: $device - 1px) {\n    @content; } }\n\n@mixin mobile {\n  @media screen and (max-width: $tablet - 1px) {\n    @content; } }\n\n@mixin tablet {\n  @media screen and (min-width: $tablet), print {\n    @content; } }\n\n@mixin tablet-only {\n  @media screen and (min-width: $tablet) and (max-width: $desktop - 1px) {\n    @content; } }\n\n@mixin touch {\n  @media screen and (max-width: $desktop - 1px) {\n    @content; } }\n\n@mixin desktop {\n  @media screen and (min-width: $desktop) {\n    @content; } }\n\n@mixin desktop-only {\n  @if $widescreen-enabled {\n    @media screen and (min-width: $desktop) and (max-width: $widescreen - 1px) {\n      @content; } } }\n\n@mixin until-widescreen {\n  @if $widescreen-enabled {\n    @media screen and (max-width: $widescreen - 1px) {\n      @content; } } }\n\n@mixin widescreen {\n  @if $widescreen-enabled {\n    @media screen and (min-width: $widescreen) {\n      @content; } } }\n\n@mixin widescreen-only {\n  @if $widescreen-enabled and $fullhd-enabled {\n    @media screen and (min-width: $widescreen) and (max-width: $fullhd - 1px) {\n      @content; } } }\n\n@mixin until-fullhd {\n  @if $fullhd-enabled {\n    @media screen and (max-width: $fullhd - 1px) {\n      @content; } } }\n\n@mixin fullhd {\n  @if $fullhd-enabled {\n    @media screen and (min-width: $fullhd) {\n      @content; } } }\n\n// Placeholders\n\n@mixin unselectable {\n  -webkit-touch-callout: none;\n  -webkit-user-select: none;\n  -moz-user-select: none;\n  -ms-user-select: none;\n  user-select: none; }\n\n%unselectable {\n  @include unselectable; }\n\n@mixin arrow($color: transparent) {\n  border: 3px solid $color;\n  border-radius: 2px;\n  border-right: 0;\n  border-top: 0;\n  content: \" \";\n  display: block;\n  height: 0.625em;\n  margin-top: -0.4375em;\n  pointer-events: none;\n  position: absolute;\n  top: 50%;\n  transform: rotate(-45deg);\n  transform-origin: center;\n  width: 0.625em; }\n\n%arrow {\n  @include arrow; }\n\n@mixin block($spacing: $block-spacing) {\n  &:not(:last-child) {\n    margin-bottom: $spacing; } }\n\n%block {\n  @include block; }\n\n@mixin delete {\n  @extend %unselectable;\n  -moz-appearance: none;\n  -webkit-appearance: none;\n  background-color: bulmaRgba($scheme-invert, 0.2);\n  border: none;\n  border-radius: $radius-rounded;\n  cursor: pointer;\n  pointer-events: auto;\n  display: inline-block;\n  flex-grow: 0;\n  flex-shrink: 0;\n  font-size: 0;\n  height: 20px;\n  max-height: 20px;\n  max-width: 20px;\n  min-height: 20px;\n  min-width: 20px;\n  outline: none;\n  position: relative;\n  vertical-align: top;\n  width: 20px;\n  &::before,\n  &::after {\n    background-color: $scheme-main;\n    content: \"\";\n    display: block;\n    left: 50%;\n    position: absolute;\n    top: 50%;\n    transform: translateX(-50%) translateY(-50%) rotate(45deg);\n    transform-origin: center center; }\n  &::before {\n    height: 2px;\n    width: 50%; }\n  &::after {\n    height: 50%;\n    width: 2px; }\n  &:hover,\n  &:focus {\n    background-color: bulmaRgba($scheme-invert, 0.3); }\n  &:active {\n    background-color: bulmaRgba($scheme-invert, 0.4); }\n  // Sizes\n  &.is-small {\n    height: 16px;\n    max-height: 16px;\n    max-width: 16px;\n    min-height: 16px;\n    min-width: 16px;\n    width: 16px; }\n  &.is-medium {\n    height: 24px;\n    max-height: 24px;\n    max-width: 24px;\n    min-height: 24px;\n    min-width: 24px;\n    width: 24px; }\n  &.is-large {\n    height: 32px;\n    max-height: 32px;\n    max-width: 32px;\n    min-height: 32px;\n    min-width: 32px;\n    width: 32px; } }\n\n%delete {\n  @include delete; }\n\n@mixin loader {\n  animation: spinAround 500ms infinite linear;\n  border: 2px solid $grey-lighter;\n  border-radius: $radius-rounded;\n  border-right-color: transparent;\n  border-top-color: transparent;\n  content: \"\";\n  display: block;\n  height: 1em;\n  position: relative;\n  width: 1em; }\n\n%loader {\n  @include loader; }\n\n@mixin overlay($offset: 0) {\n  bottom: $offset;\n  left: $offset;\n  position: absolute;\n  right: $offset;\n  top: $offset; }\n\n%overlay {\n  @include overlay; }\n","// Colors\n\n$black:        hsl(0, 0%, 4%) !default;\n$black-bis:    hsl(0, 0%, 7%) 